import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, NavigationExtras, Router } from '@angular/router';
import { ActionSheetController, LoadingController, Platform, PopoverController } from '@ionic/angular';
import { Additional, FoodDrinkCategory, FoodDrinkFood, FoodDrinkMenu, OrderItem } from 'src/app/objects/food-drink-menu.model';
import { Message,MenuVisitor } from 'src/app/objects/message.model';
import { MessageService } from 'src/app/services/message.service';
import { Location } from '@angular/common';
import * as firebase from 'firebase';
import { AuthService } from 'src/app/services/auth.service';
import { UrlService } from 'src/app/services/url.service';
import { CartService } from 'src/app/services/cart.service';
import { ToastController } from '@ionic/angular'; 
import { CartItem } from 'src/app/objects/cart.model';
import { Channel } from 'src/app/objects/channel.model';
import { ChannelService } from 'src/app/services/channel.service';
import { AngularFirestore } from '@angular/fire/firestore';


@Component({
  selector: 'app-food-drink-menu',
  templateUrl: './food-drink-menu.page.html',
  styleUrls: ['./food-drink-menu.page.scss'],
})
export class FoodDrinkMenuPage implements OnInit {
  cid             :string;
  mid             :string;
  message         :Message;
  foodDrinkMenu   :FoodDrinkMenu      = new FoodDrinkMenu(); 
  activeCategory  :FoodDrinkCategory  = new FoodDrinkCategory();
  activeCategoryIndex:number = 0;
  nextCategory    :FoodDrinkCategory;
  prevCategory    :FoodDrinkCategory;
  layout          :string = 'withimage'
  backBtnSub      :any;
  previousUrl     :string = '';
  showAdditionals :FoodDrinkFood;
  cartItemCount   :number=0;
  channel         :Channel;
  loading         :HTMLIonLoadingElement;

  constructor(
    private fs:AngularFirestore,
    private channelService:ChannelService,
    private toastController:ToastController,
    public route: ActivatedRoute, 
    private actionSheetController:ActionSheetController,
    private popoverController: PopoverController,
    public location:Location,
    public authService:AuthService,
    private platform:Platform,
    private urlService:UrlService,
    private cartService:CartService,
    private messageService:MessageService,
    private loadingController:LoadingController,
    public router: Router) { 
  }

  async ngOnInit() {  
    this.loading = await this.loadingController.create({ 
      message: 'Please wait...',
      duration: 5000
    }) 
    this.loading.present();

    this.cid  = this.route.snapshot.paramMap.get('cid')
    this.mid  = this.route.snapshot.paramMap.get('mid')

    this.urlService.previousUrl$.subscribe((previousUrl: string) => {
      this.previousUrl = previousUrl
    });
    this.backBtnSub = this.platform.backButton.subscribe(() => { 
      this.back()
    });

    this.messageService.getMessageById(this.cid,this.mid).ref.get().then((snapshot)=>{
      let menuVisitor:MenuVisitor = new MenuVisitor();
      let uid = 'anonim_visitor';
      if(this.authService.USER){
        uid = this.authService.USER.uid
      }
      menuVisitor.uid = uid;
      menuVisitor.created_at = firebase.firestore.FieldValue.serverTimestamp();
      snapshot.ref.collection('visitors').add({...menuVisitor}) 
      this.message = <Message>snapshot.data()
      this.foodDrinkMenu = this.message.food_drink_menu;
      this.activeCategory = this.foodDrinkMenu.categories[0]; 
      if(this.foodDrinkMenu.categories[1]){
        this.nextCategory = this.foodDrinkMenu.categories[1];
      } 
    }) 
    await this.channelService.getChannelInfo(this.cid).ref.get().then((channel)=>{
      this.channel = channel.data()
    })
    this.loading.dismiss();
    
    
    this.getCart()
  }

  getCart(){ 
    this.cartService.getCart(this.cid,this.channel).valueChanges().subscribe(data=>{  
      this.cartItemCount =data.length; 
    })
  }

  changeCategory(artim:string){ 
    if(artim == '-'){ 
      this.activeCategory = this.prevCategory;
    }else{ 
      this.activeCategory = this.nextCategory;
    }

    this.foodDrinkMenu.categories.map((category,i)=>{
      if(category == this.activeCategory){
        if(this.foodDrinkMenu.categories[i+1]){
          this.nextCategory = this.foodDrinkMenu.categories[i+1]
        }else{
          this.nextCategory =null
        }

        if(this.foodDrinkMenu.categories[i-1]){
          this.prevCategory = this.foodDrinkMenu.categories[i-1]
        }else{
          this.prevCategory =null
        }
      }
    }) 
  }

  async openCategoryList(){
    let action_menu=[];
    this.foodDrinkMenu.categories.forEach((c,i)=>{
      action_menu.push({text:c.title,role:'destructive',handler: () => {
        this.activeCategory = c;
        this.foodDrinkMenu.categories.map((category,i)=>{
          if(category == this.activeCategory){
            if(this.foodDrinkMenu.categories[i+1]){
              this.nextCategory = this.foodDrinkMenu.categories[i+1]
            }else{
              this.nextCategory =null
            }
    
            if(this.foodDrinkMenu.categories[i-1]){
              this.prevCategory = this.foodDrinkMenu.categories[i-1]
            }else{
              this.prevCategory =null
            }
          }
        })
      }})
    })

    action_menu.push({text: 'Men√ºden √áƒ±k',
      icon: 'close',
      role: 'destructive', 
      handler: () => {
          this.back()
        }
      })

    const actionSheet = await this.actionSheetController.create({
      header: 'Kategoriler',
      cssClass: 'my-custom-class',
      buttons: action_menu
    });
    await actionSheet.present();
  } 

  back(){
    if(this.previousUrl == undefined || this.previousUrl == null){
      if(this.backBtnSub){
        this.backBtnSub.unsubscribe();
      }
      this.router.navigate(['home']);
    }else{
      this.location.back();
    }
  }

  ngOnDestroy(){
    if(this.backBtnSub){
      this.backBtnSub.unsubscribe();
    }
  }

  addToFood(additional:Additional,food:FoodDrinkFood){  
    let exist = false;
    food.orders.map((order,i)=>{
      if(order.additional == additional){
        order.additional.quantity++;
        exist = true;
      }
    })

    if(!exist){
        additional.quantity = 1
        food.orders.push({additional:additional})
    } 
  }

  removeFromFood(additional:Additional,food:FoodDrinkFood){

    food.orders = food.orders.filter((order,i)=>{
      if(order.additional == additional && additional.quantity>1){
        additional.quantity--;  
        return order;
      }else if(order.additional == additional && additional.quantity==1){
        additional.quantity = 0
        return 
      }else{
        return order;
      }
    }) 
  }

  getFoodTotal(food:FoodDrinkFood){
    let total:number        = 0;
    food.orders.map(orderitem=>{
        total += orderitem.additional.quantity*orderitem.additional.price
    })
    return food.price+total;
  }

  addToCart(food:FoodDrinkFood){
    let  uid      = this.authService.USER?.uid;
    if(!uid){
      if(window.localStorage.getItem('uid')){
        uid = window.localStorage.getItem('uid')
      }else{
        uid = this.fs.createId();
        window.localStorage.setItem('uid',uid)
      }
    }

    const created_at  = firebase.firestore.FieldValue.serverTimestamp();
    const cartItem:CartItem        = {uid:uid,created_at:created_at,food:{...food}};
    this.cartService.addToCart(cartItem,this.cid).then(async ()=>{
      const toast = await this.toastController.create({
        color: 'success',
        message: "Yemek sepete eklendi",
        duration: 3000
      });
      toast.present();
    })
  }
  
  checkout(){ 
    let navigationExtras: NavigationExtras = {
      state: {
        cid: this.cid
      }
    };
    this.router.navigate(['cart'],navigationExtras)
  }

  showCutomerOrders(){
    let  uid  = this.authService.USER?.uid;
    if(!uid){
      if(window.localStorage.getItem('uid')){
        uid   = window.localStorage.getItem('uid')
      }else{
        uid   = this.fs.createId();
        window.localStorage.setItem('uid',uid)
      }
    }
    let navigationExtras: NavigationExtras = {
      state: {
        uid: uid,
        cid: this.cid
      }
    };
    this.router.navigate(['customer-orders'],navigationExtras) 
  }

}